@font-face {
  font-family: 'Beckan';
  src: url('../assets/fonts/Beckan\ Regular.ttf') format('truetype');
  font-weight: 100;
  font-style: normal;
}

@font-face { 

  font-family: 'Playfair-Display';
  src: url('../assets/fonts/PlayfairDisplay-VariableFont_wght.ttf') format('truetype');
  font-weight: 100;
  font-style: normal;

}

.video-container {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  z-index: -1; /* Behind main content unless toggled */
  opacity: 0;
  transition: opacity 3s ease-in-out, filter 3s ease-out;
  filter: blur(8px);

}

.video-container.active {
  z-index: 0;
  opacity: 1;
  filter: blur(0);
}

.bg-video {
  position: absolute;
  top: 50%;
  left: 50%;
  min-width: 100%;
  min-height: 100%;
  width: auto;
  height: auto;
  transform: translate(-50%, -50%);
  object-fit: cover;
  filter: brightness(0.5); /* Optional: Dim the video for legibility */
}

.centered-div {

  all: unset;
  user-select: none;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 1;
  width: 25rem;
  
  background-color: rgba(0, 0, 0, 0.8);  /* Or use a gradient for more depth */

  border-radius: 12px;
  color: white;
  text-align: center;
  transition: all 0.2s ease;

}

.centered-div h1, h2 {

font-family: 'Beckan';
font-weight: 100;
font-size: 36px;

}

.centered-div p {

  
  font-family: 'Playfair-Display';
  font-weight: lighter;
}

.centered-div * {

  padding: .5rem;
  
  
}


